body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  font-family: inherit;
}

.App {
  font-family: Helvetica, Arial, sans-serif;
  height: 100vh;
  min-width: -webkit-fill-available;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #F0F0F0;
  padding: 12px;
  overflow: hidden
}

label {
  color: #727272;
  font-size: 14px;
  margin-bottom: 4px;
}

textarea {
  border-radius: 0;
  border: none;
  border-bottom: 1px solid #ededed;
  font-size: 14px;
  line-height: 1.5;
  padding: 8px 0;
  resize: none;
  transition: border-color 150ms ease;
  width: 100%;
}

textarea:hover {
  border-color: #c6c6c6;
}

textarea:focus {
  outline: none;
  border-color: #c6c6c6;
}

textarea::placeholder {
  color: #c6c6c6;
}

.textInputContainer {
  padding: 4px;
  border-radius: 4px;
  border: 1px solid lightgray;
  background: #FFFFFF;
  width: -webkit-fill-available;
}

.textInput {
  display: flex;
  gap: 10px;
  padding: 4px;
}

.buttonNewTopics:hover {
  background-color: #3A96DD;
}

.buttonNewTopics {
  border: none;
  color: black;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  width: 20px;
  border-radius: 10px;
}

.containerInputActions {
  display: flex;
  justify-content: end;
}

.conversationContainer {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;
  overflow: overlay;
  background: #FAFAFA;
  flex: 1;
}

.statusBar {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  padding: 4px;
  border-radius: 4px;
  background-color: lightgray;
  align-self: flex-end;
  font-size: 8px;
}

.messageAvatar {
  min-width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-content: center;
}

.message {
  display: flex;
  flex-direction: column;
  border-radius: 4px 4px 0px 0px;
  background-color: #ffffff;
}

.messageTitle {
  display: flex;
  flex-direction: row;
  white-space: break-spaces;
  text-align: left;
  align-self: center;
  background-color: #E0EDFF;
  padding: 4px;
  border-radius: 4px 4px 0px 0px;
  width: 100%;
  height: 32px;
  gap: 4px
}

.messageContent {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-weight: 400;
  white-space: break-spaces;
  text-align: left;
  padding: 8px;
  font-family: math;
  align-self: center;
  width: -webkit-fill-available;
}

.messageStatus {
  align-self: center;
  font-size: 8px;
  font-weight: 400;
  display: flex;
  flex: 1;
  justify-content: right;
  gap: 10px;
}

.companyEntity {
  font-size: 12px;
  font-weight: 400;
  margin: 2px;
  display: flex;
  gap: 4px;
  flex-direction: column;
}

.companyInfo {
  font-size: 10px;
  font-weight: 400;
  margin: 2px;
  display: flex;
  gap: 4px;
}

/**on hover, change color to lighter then #2169EB **/

.messageToggle:hover {
  background-color: #3A96DD;
}

.messageToggle {
  height: 20px;
  width: 20px;
  background: #2169EB;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  cursor: pointer;
  text-align: center;
}

.messageActionBar {
  display: flex;
  /* gap: 0px;
  padding: 4px;
  border-radius: 4px;
  background-color: lightgray; */
  justify-content: flex-end;
}

.messageSummaryTitle {
  font-size: 14px;
  font-weight: 600;
  margin: 2px;
}

.messageSummaryKeyPointTitle {
  font-weight: 400;
  padding: 2px;
  margin: 2px;
  border-radius: 4px;
  cursor: pointer;
  background-color: #FAFAFA;
}

.messageSummaryKeyPointTitle:hover {
  background-color: lightblue;
}

.messageSummaryKeyPointStatement {
  font-weight: 400;
  margin: 4px;
}

.historyMessageContainer {
  display: flex;
  flex-direction: column;
  padding: 4px;
  border-radius: 4px;
  background-color: #f0f0f0;
  height: 60px;
  font-size: 14px;
  overflow: overlay;
}

.historyMessage {
  display: flex;
  flex-direction: column;
  padding: 4px;
  cursor: pointer;
}

.historyMessage:hover {
  background-color: #E0EDFF;
}

.table {
  width: -webkit-fill-available;
  border-collapse: collapse;
  margin: 4px;
  font-size: 16px;
  font-family: math;
  text-align: left,
}

.table-td {
  border: 1px solid #dddddd;
  padding: 4px;
}

.hoverPanel {
  width: 100%;
  height: 30px;
  background-color: floralwhite;
}

.header {
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-radius: 4px;
  font-size: 14px;
  width: -webkit-fill-available;
  background: none;
  text-align: -webkit-center;
}

.command {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 10px;
  padding: 4px;
  border-radius: 4px;
  background-color: #3275F0;
  align-self: flex-end;
  font-size: 14px;
  cursor: pointer;
}

.commandList {
  gap: 10px;
  color: #FFFFFF;
  display: flex;
}

.abstract {
  display: flex;
  padding: 4px;
  border-radius: 4px;
}

.abstractTag {
  display: flex;
  padding: 4px;
  border-radius: 4px;
  background-color: #2169EB;
  color: #FFFFFF;
  height: fit-content;
}

.tabContainter {
  display: flex;
  flex-direction: row;
  gap: 10px;
  font-size: 16px;
  justify-content: flex-start;
  font-family: math;
  color: #FFFFFF;
  margin: 8px 8px 0px 8px
}

.tab {
  padding: 8px;
  border-radius: 8px 8px 0px 0px;
  cursor: pointer;
  width: fit-content;
  font-size: 14px;
  background: #0000002F;
  color: #000;
}

.active {
  background: #3275F0;
  color: #FFFFFF;
}

.tab:hover {
  background: #3A96DD;
  color: #FFFFFF;
}

.demoItem {
  padding: 4px;
  text-decoration: underline;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
}

.demoItem:hover {
  background-color: #E0EDFF;
}

.demoList {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 14px;
  overflow: overlay;
}

.articleTitle {
  padding: 4px;
  border-radius: 16px;
  background: lightgray;
}

.eventSummaryCard {
  align-items: center;
  border-radius: 8px;
  display: flex;
  font-size: small;
  height: auto;
  flex-direction: column;
  margin: 0px;
  width: -webkit-fill-available;
  gap: 4px;
  align-items: start;
}

.eventContent {
  font-size: 14px;
  line-height: 20px;
}

.earningBeat {
  font-family: fantasy;
  font-size: medium;
  background: -webkit-linear-gradient(45deg, #09009f, #119e63 80%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: -webkit-fill-available;
}

.earningMiss {
  font-family: fantasy;
  font-size: medium;
  background: -webkit-linear-gradient(45deg, #ff0000, #ff8c00 80%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: -webkit-fill-available;
}

.card {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  /* gap: 10px; */
  border-radius: 8px;
  box-shadow: 0 2px 4px #0000001a;
  margin: 8px;
  background: #0000000A;
}

.cardFooter {
  display: flex;
  justify-content: end;
  padding: 4px 10px;
}

.cardHeader {
  display: flex;
  padding: 10px;
  border-radius: 8px 8px 0px 0px;
  background: linear-gradient(140.53deg, rgb(240, 225, 247) 9.32%, rgb(205, 223, 255) 58.45%);
  gap: 4px;
  color: black;
  justify-content: space-between;
  font-size: 14px;
  display: flex;
  flex-direction: column;
}

.cardContent {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  padding: 10px 10px 0px 10px;
  background: #FFFFFF;
  margin: 0px 8px 8px 8px;
  padding: 8px;
  gap: 4px;
}

.seeMore {
  background: linear-gradient(140.53deg, rgb(240, 225, 247) 9.32%, rgb(205, 223, 255) 58.45%);
  ;
  padding: 4px 8px;
  border-radius: 16px;
  font-size: 12px;
  cursor: pointer;
}

.factsCardContainer {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.factCard {
  align-items: center;
  display: flex;
  flex-direction: column;
  background: linear-gradient(45deg, #d7e7f5, #f0f8ff);
  border-radius: 8px;
  box-shadow: 0 2px 4px #0000001a;

  gap: 4px;
  padding: 6px;
  align-items: self-start;
  font-size: 14px;
}

.hoverable {
  border: 1px solid transparent;
  cursor: pointer;
}

.hoverable:hover {
  border: 1px solid #3275F0;
}

.icon {
  height: 20px;
  border-radius: 15%;
}

.stockHeader {
  display: flex;
  align-items: center;
  gap: 8px;
  justify-content: space-between;
}

.conceptNamesContainer {
  display: flex;
  gap: 1rem;
  justify-content: flex-start;
  margin: 8px;
  align-items: center;
  overflow: hidden;
}

.profile {
  font-size: small;
}

.dataContainer {
  background: #FFF;
  padding: 10px;
  margin: 0px 10px 10px 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.dataViewHeader {
  display: flex;
  align-items: center;
  width: -webkit-fill-available;
  font-weight: 600;
}

.dataViewHeaderButtonContainer {
  display: flex;
  gap: 10px;
  flex: 1;
  justify-content: flex-end;
}

.opinionHeader {
  padding: 4px;
  margin-right: 4px;
  background-color: #3275f0;
  color: #FFF;
  font-size: 16px;
  border-radius: 8px;
}

.opinion {
  margin: 10px;
  line-height: 20px;
  font-family: math;
}

.opinionBody {}